import Image from "next/image";
import { notFound } from "next/navigation";
import { allBooks } from "@/lib/sample-data";
import { Button } from "@/components/ui/button";
import { Badge } from "@/components/ui/badge";
import { Separator } from "@/components/ui/separator";
import { Star, Truck, ShieldCheck, Plus, Minus } from "lucide-react";
import {
  Card,
  CardContent,
  CardDescription,
  CardHeader,
  CardTitle,
} from "@/components/ui/card";
import BookCard from "@/components/BookCard";

export function generateStaticParams() {
  return allBooks.map((book) => ({
    id: book.id,
  }));
}

export default function BookDetailPage({ params }: { params: { id: string } }) {
  const book = allBooks.find((b) => b.id === params.id);

  if (!book) {
    notFound();
  }
  
  const relatedBooks = allBooks.filter(b => b.category === book.category && b.id !== book.id).slice(0,4);

  return (
    <div className="container mx-auto px-4 py-12">
      <div className="grid md:grid-cols-2 gap-8 lg:gap-12">
        <div className="flex justify-center items-start">
          <Image
            src={book.imageUrl}
            alt={`Cover of ${book.title}`}
            width={500}
            height={750}
            className="rounded-lg shadow-xl aspect-[2/3] object-cover"
            data-ai-hint="book cover"
          />
        </div>
        <div>
          <Badge variant="secondary">{book.category}</Badge>
          <h1 className="text-4xl lg:text-5xl font-bold font-headline mt-2">
            {book.title}
          </h1>
          <p className="text-xl text-muted-foreground mt-2">by {book.author}</p>

          <div className="flex items-center gap-4 mt-4">
            <div className="flex items-center gap-1">
              {[...Array(5)].map((_, i) => (
                <Star
                  key={i}
                  className="w-5 h-5 text-yellow-400 fill-current"
                />
              ))}
            </div>
            <span className="text-sm text-muted-foreground">(1,203 reviews)</span>
          </div>

          <Separator className="my-6" />

          <p className="text-lg leading-relaxed">{book.description}</p>

          <div className="mt-6">
            <p className="text-3xl font-bold text-primary">
              ${book.price.toFixed(2)}
            </p>
            <p
              className={`text-sm font-medium mt-1 ${
                book.stock > 0 ? "text-green-600" : "text-red-600"
              }`}
            >
              {book.stock > 0 ? `${book.stock} in stock` : "Out of stock"}
            </p>
          </div>

          <Card className="mt-6 bg-secondary/50">
            <CardContent className="p-4">
              <div className="flex items-center justify-between">
                <div className="flex items-center gap-2 text-sm text-muted-foreground">
                  <span>Quantity</span>
                  <Button variant="outline" size="icon" className="h-8 w-8">
                    <Minus className="h-4 w-4" />
                  </Button>
                  <span className="font-bold text-lg">1</span>
                  <Button variant="outline" size="icon" className="h-8 w-8">
                    <Plus className="h-4 w-4" />
                  </Button>
                </div>
                <Button size="lg" disabled={book.stock === 0}>
                  Add to Cart
                </Button>
              </div>
            </CardContent>
          </Card>

          <div className="mt-6 space-y-3 text-sm">
            <div className="flex items-center gap-2">
              <Truck className="w-5 h-5 text-muted-foreground" />
              <span className="text-muted-foreground">
                Free shipping on orders over $50
              </span>
            </div>
            <div className="flex items-center gap-2">
              <ShieldCheck className="w-5 h-5 text-muted-foreground" />
              <span className="text-muted-foreground">
                30-day return policy
              </span>
            </div>
          </div>
        </div>
      </div>
      
      {relatedBooks.length > 0 && (
         <div className="mt-20">
            <h2 className="text-3xl font-bold font-headline mb-6">Related Books</h2>
            <div className="grid grid-cols-1 gap-x-6 gap-y-10 sm:grid-cols-2 lg:grid-cols-4 xl:gap-x-8">
                {relatedBooks.map((relatedBook) => (
                    <BookCard key={relatedBook.id} book={relatedBook} />
                ))}
            </div>
         </div>
      )}

    </div>
  );
}
